<template>
  <div>
    <v-container>
      <v-row class='justify-center'>
        <v-col :cols='4'>
          <v-card>
            <v-card-title>Images Upload</v-card-title>
            <v-card-text>
              <v-btn color="primary" @click="submit()">Upload</v-btn> 
              <v-file-input
                small-chips
                multiple
                show-size
                accept="image/jpeg"
                prepend-icon="mdi-camera"
                label="Images"
                v-model="images"
              ></v-file-input> 
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-container v-if='uploading'>
        <v-row class='justify-center'>
          <v-col 
            :cols=6
            class='text-center mt-4'>
            <v-progress-circular
              indeterminate
              color="black"
            ></v-progress-circular>
          </v-col>
        </v-row>
      </v-container>
  </div>
</template>

<script>
import imagesApi from '@/api/images';

export default {
  name: 'ImageUpload',
  data: () => {
    return {
      images: null,
      uploading: false
    }
  },
  methods: {
    submit() {

      let formData = new FormData();
      for (let file of this.images) {
          formData.append("images", file, file.name);
      }

      this.uploading = true;
      imagesApi.create(formData).then(() => {
            this.uploading = false;
            this.$router.push({path: '/images'});
          })
          .catch(error => {
            this.uploading = false;
            console.log(error)
            alert("Errored while uploading image. Try again.");
          });
    }
  }
}
</script>

